import Vue from 'vue'
import Router from 'vue-router'
// IQ
import login from '@/components/home/IQ_file'


Vue.use(Router)

export default new Router({
	routes: [
		// 
		{
			path: '/questionnaire',
			name: 'questionnaire',
			component: login
		},
		// 
		{
			path: '/',
			redirect: '/questionnaire'
		}
	]
})



