var ajaxSubmit = {};
var dateStram = (new Date()).getTime();
ajaxSubmit.allUrl = {
  // eq,
  "login_eq": "eq/",
  // 
  "login_iq": "iq/",
  // 
  "login": "quick-login/",
  // 
  "message": "sms_code/",
  // 
  "consultation": "consultant/"


}
ajaxSubmit.ajax = function(option) {
  //data
  option.data = option.data ? option.data : {};
  //url, data, success, cache, alone, async, type, dataType, error
  var url = option.url;
  var data = option.data;
  var type = option.type || 'post'; //
  var dataType = option.dataType || 'json'; //
  var async = option.async || true; //
  var cache = option.cache || false; //
  var contentType = option.contentType || 'application/x-www-form-urlencoded; charset=UTF-8';
  var success = function(data) {
    option.success ? option.success(data) : "";
  };
  var error = function(jqXHR, textStatus, errorThrown) {
    option.error ? option.error() : "";
  };
  $.ajax({
    url: url,
    data: data,
    type: type,
    contentType: contentType,
    dataType: dataType,
    async: async,
    timeout: 50000,
    success: success,
    error: error,
    beforeSend: function(XMLHttpRequest) { 
         
        },
  });
}

export default ajaxSubmit;



